@import "https://use.typekit.net/jmk3xov.css";
:root {
  --dark-bg: #0f0f0ff2;
  --spacing: 100px;
  font-family: brandon-grotesque, sans-serif;
  font-style: normal;
  font-weight: 400;
}

main {
  color: #fff;
  z-index: 99;
  grid-template-columns: repeat(12, 1fr);
  width: 100%;
  margin: 0 auto;
  padding: 120px 0;
  display: grid;
  position: absolute;
}

h1, h2, h3, blockquote {
  font-family: elevon, sans-serif;
  font-style: normal;
  font-weight: 700;
}

canvas {
  position: fixed;
  top: 0;
  left: 0;
}

header {
  background: var(--dark-bg);
  margin-bottom: var(--spacing);
  grid-column: 2 / span 5;
  padding: 2rem;
  font-size: 2.5rem;
}

section {
  background: var(--dark-bg);
  margin-bottom: var(--spacing);
  grid-column: 2 / 8;
  padding: 1rem;
  font-size: 1.25rem;
  line-height: 1.5;
}

blockquote {
  margin: 0 auto;
  margin-bottom: var(--spacing);
  grid-column: 2 / span 9;
  padding: 0;
}

blockquote p {
  color: #000;
  background-color: #fff;
  font-size: 3rem;
  line-height: 1;
  display: inline;
}

.left {
  grid-column: 6 / 12;
}

.left a {
  color: #007bff;
  text-decoration: none;
}

.left a:hover {
  color: #0056b3;
}

